html {
	box-sizing: border-box; }

*, *::before, *::after {
	box-sizing: inherit; }

// html, body
// 	height: 100%

body {
	font-family: $default-font;
	font-size: 16px;
	line-height: 1;
	color: #000;
	overflow-x: hidden;
	background-color: #fff; }

::selection {
	background: #b3d4fc;
	text-shadow: none; }

img {
	display: block;
	max-width: 100%;
	height: auto; }

h1, h2, h3, h4, h5, h6 {
	margin: 0; }

p {
	margin: 0; }

ul,ol {
	margin: 0;
	padding: 0; }

ul li {
	list-style: none; }

textarea {
	resize: none; }

.container {
	max-width: 1170px;
	margin: 0 auto;
	@include r(1200) {
		margin: 0 15px; } }

.header-container {
	max-width: 1500px;
	margin: 0 auto;
	@include clr;
	@include r(1500) {
		margin: 0 15px; } }

.first-page {
	min-height: rem(850);
	// height: 100vh

	background-position: 50% 0;
	background-repeat: no-repeat;
	background-size: cover;

	position: relative;
	&::before {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(#222, .6);
		content: ''; } }

.header__logo {
	float: left;
	position: relative;
	top: rem(35);
	@include r(900) {
		float: none;
		.logo__img {
			margin: 0 auto;
			margin-bottom: rem(20); } } }

.header__menu {
	float: right;
	position: relative;
	top: rem(42);
	@include r(900) {
		float: none;
		text-align: center; } }

.firs-page__center-block {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	content: ''; }

.menu__item {
	display: inline-block;
	vertical-align: middle;
	@include r(570) {
		display: block;
		margin-bottom: rem(20); } }

.menu__link {
	padding: rem(10) rem(30);
	font-size: em(16);
	font-weight: 400;
	text-decoration: none;
	color: #f9fafe;
	transition: .35s linear;
	&:hover {
		color: $accent; }
	&_bordered {
		border: 1px solid #f9fafe;
		border-radius: 5px;
		&:hover {
			border-color: $accent; } } }

.center-block {
	text-align: center; }

.center-block__title {
	font-size: em(72);
	font-weight: 700;
	color: #f9fafe;
	margin-bottom: rem(10);
	@include r(900) {
		font-size: em(50); } }

.center-block__subtitle {
	display: inline-block;
	font-family: $alt-font;
	font-size: em(30);
	font-weight: 400;
	color: #f9fafe;
	text-transform: uppercase;
	margin-bottom: rem(88);
	@include r(900) {
		font-size: em(22); } }

.search__input {
	display: inline-block;
	vertical-align: middle;
	max-width: rem(570);
	width: 100%;
	border: none;
	padding: rem(21) rem(10);
	font-size: em(16);
	@include placeholder(#717171);
	color: #717171;
	text-transform: uppercase;
	border-radius: 5px 0 0 5px;
	@include r(1555) {
		text-align: center;
		border-radius: 0; }
	&:focus {
		outline: 0; }
	&_soc {
		max-width: rem(270);
		// width: 100%
		padding: 0;
		color: #717171;
		font-family: $alt-font;
		font-size: em(14);
		font-weight: 400;
		line-height: lh(22,14);
		padding: rem(14) rem(20); } }

.search__button {
	display: inline-block;
	vertical-align: middle;
	text-align: center;
	position: relative;
	left: -5px;
	max-width: 200px;
	width: 100%;
	height: rem(60);
	line-height: rem(60);
	font-size: em(14);
	background-color: #e7434e;
	border-radius: 0px 5px 5px 0px;
	border: 0;
	outline: 0;
	padding: 0;
	color: #fff;
	font-weight: 700;
	cursor: pointer;
	transition: .75s linear;
	@include r(1555) {
		border-radius: 0; }
	&:hover {
		background-color: orange; }
	&::before {
		// position: absolute
		display: inline-block;
		vertical-align: middle;
		margin-right: rem(12);
		content: '';
		@include sprite($s-boat);
		z-index: 100; }
	&_soc {
		max-width: rem(145);
		height: rem(50);
		line-height: rem(14);
		&::before {
			display: none; } } }

.destinations {
	position: relative;
	text-align: center; }

.statistic {
	background-color: white;
	border-radius: 5px;
	box-shadow: 0 0 30px rgba(52, 52, 52, 0.15);
	padding: rem(40) rem(80); }

.destinations__statistic {
	position: relative;
	top: rem(-72); }

.statistic__count {
	display: inline-block;
	vertical-align: top;
	width: 23%;
	// margin-right: rem(200)
	@include r(780) {
		width: 45%;
		margin-bottom: rem(14); }
	@include r(560) {
		width: 100%; } }

.statistic__counter {
	font-family: $alt-font;
	display: inline-block;
	font-size: em(30);
	font-weight: 700;
	color: #343434;
	text-transform: uppercase;
	margin-bottom: rem(15); }

.statistic__counter-name {
	color: #717171;
	font-family: $alt-font;
	font-size: em(18);
	font-weight: 400;
	line-height: lh(22,18); }

.destinations__title {
	margin: rem(30) 0;
	font-size: em(30);
	font-weight: 400;
	color: #343435; }

.destinations__description {
	font-family: $alt-font;
	font-size: em(16);
	font-weight: 400;
	color: #717171;
	margin-bottom: rem(80); }

.prev__container {
	@include clr;
 }	// padding: 0 rem(15)

.prev__item {
	position: relative;
	height: rem(360);
	border-radius: 5px;
	float: left;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50% 0;
	margin-right: rem(30);
	margin-bottom: rem(30);
	&:last-child {
		margin-right: 0; }
	@include r(1200) {
		margin-right: 0; } }

.prev__item_small {
	width: rem(370);
	@include r(1200) {
		width: 33.3%; } }
.prev__item_medium {
	width: rem(570);
	@include r(1200) {
		width: 49%; } }
.prev__item_big {
	width: rem(770);
	@include r(1200) {
		width: 66%; } }

.prev__inner-count {
	display: block;
	width: rem(82);
	height: rem(42);
	line-height: rem(42);
	background-color: #e7434e;
	border-radius: 5px;
	position: absolute;
	left: rem(25);
	top: rem(25);
	content: '';
	font-family: $alt-font;
	font-size: em(16);
	font-weight: 700;
	text-transform: uppercase;
	text-decoration: none;
	text-align: center;
	color: #fff;
	transition: .75s linear;
	&:hover {
		background-color: orange; }
	&:before {
		@include sprite($s-boat);
		display: inline-block;
		vertical-align: baseline;
		margin-right: rem(10);
		content: '';
		@include r(800) {
			margin-right: rem(5); } }
	@include r(800) {
		left: rem(10);
		top: rem(10);
		font-size: em(14);
		width: rem(60);
		height: rem(32);
		line-height: rem(32); } }

.prev__inner-name {
	position: absolute;
	left: rem(25);
	bottom: rem(25);
	content: '';
	text-align: left; }

.prev__country {
	font-size: em(30);
	font-weight: 400;
	color: #fff;
	margin-bottom: rem(10);
	@include r(800) {
		font-size: em(18);
		font-weight: 700; } }

.prev__city {
	font-size: em(20);
	color: #fff;
	@include r(800) {
		font-size: em(12);
		font-weight: 700; } }

.btn {
	display: inline-block;
	max-width: rem(312);
	width: 100%;
	text-align: center;
	height: rem(60);
	line-height: rem(62);
	border: 1px solid #bdbdbd;
	text-decoration: none;
	font-weight: 700;
	font-size: em(14);
	border-radius: 5px;
	text-transform: uppercase;
	color: #343434;
	transition: .5s linear;
	box-shadow: 0px 0px 7px 0px #ccc;
	@include r(800) {
		font-size: em(10);
		height: rem(40);
		line-height: rem(42);
		max-width: rem(212); }
	&:hover {
		background-color: $accent;
		border-color: $accent; }
	&_red {
		background-color: red;
		box-shadow: 0px 0px 0px 0px #ccc;
		border-color: #e7434e;
		color: #fff; } }

.btn-warap {
	margin: rem(70) 0;
	@include r(800) {
		margin: rem(40) 0; } }

.boat {
	min-height: rem(600);
	position: relative;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50% 100%;
	text-align: center;
	&::before {
		position: absolute;
		left: 0;
		right: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(#222, .5);
		content: ''; } }

.boat__center {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	content: ''; }

.boat__title {
	font-size: em(72);
	font-weight: 700;
	color: #fff;
	margin-bottom: rem(20);
	@include r(980) {
		font-size: em(44); }
	@include r(600) {
		font-size: em(22); } }

.boat__descr {
	font-family: $alt-font;
	display: inline-block;
	font-weight: 400;
	font-size: em(30);
	color: #fff;
	margin-bottom: rem(80);
	text-transform: uppercase;
	@include r(980) {
		font-size: em(24);
		margin-bottom: rem(55); }
	@include r(600) {
		font-size: em(18);
		margin-bottom: rem(25); } }


.featured {
	text-align: center;
	padding: rem(90) 0; }

.featured__title {
	font-size: em(30);
	font-weight: 400;
	color: #343435;
	margin-bottom: rem(20); }

.featured__description {
	font-family: $alt-font;
	font-size: em(16);
	font-weight: 400;
	color: #717171;
	margin-bottom: rem(80); }

.btn-wrap {
	margin-top: rem(69); }

.footer {
	background-color: #404040;
	background-image: url("../img/footer-sprite.jpg");
	background-repeat: repeat-x;
	background-position: 0 0;
	// height: rem(325)
	padding: rem(60) 0; }

.footer {
	height: 374px;
	position: fixed;
	bottom: 0;
	z-index: 100;
	left: 0;
	right: 0;
 }	// text-align: center

.wrapper {
	background: #fff;
	position: relative;
	z-index: 200;
	box-shadow: 0 0 10px rgba(#000,.5); }

body {
	padding-bottom: 374px; }

.footer__item {
	float: left;
	// display: inline-block
	vertical-align: top;
	width: 20%;
	// text-align: left
	// width: rem(175)
	// margin-right: rem(70)
	color: #fff;
	margin-bottom: rem(20);
	&:last-child {
		margin-right: 0; }
	&_soc {
		// width: rem(420)
		width: 38%; } }


.footer .container {
	@include clr; }

.footer__title {
	font-weight: 700;
	font-size: em(14);
	margin-bottom: rem(24); }

.footer__link {
	font-family: $alt-font;
	font-weight: 400;
	font-size: em(14);
	color: #fff;
	text-decoration: none;
	word-wrap: wrap;
	transition: .5s linear;
	&:hover {
		color: $accent; } }

.footer__items {
	margin-bottom: rem(20);
	color: #fff;
	&:last-child {
		margin-bottom: 0; } }

.soc__title {
	margin-bottom: rem(20);
	@include r(1200) {
		text-align: center; } }

.soc__search {
	margin-bottom: rem(36);
	@include r(1200) {
		text-align: center; } }

.soc__socials {
	// max-width: rem(400)
	width: 100%;
	@include r(1200) {
		text-align: center; } }

.soc__item {
	display: inline-block;
	position: relative;
	width: rem(44);
	height: rem(44);
	line-height: rem(44);
	background-color: #717171;
	border-radius: 50%;
	margin-right: rem(18);
	box-shadow: inset 0 0 5px rgba(#000, .75);
	transition: .5s linear;
	&:hover {
		background-color: $accent; }
	&::before {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		content: ''; } }
.soc__item.fb::before {
	@include sprite($s-fb); }
.soc__item.tw::before {
	@include sprite($s-tw); }
.soc__item.google::before {
	@include sprite($s-google); }
